import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import LoginForm from "../component/LoginForm";
import { addUserData, clearDelAccount } from "../redux/action";
import { connect } from "react-redux";
import { Alert } from "react-bootstrap";
import Delayed from "../component/Delayed";
import { GoogleLogin } from "react-google-login";
import { clientId } from "../config/security";

const styles = {
  container: {
    top: "30%",
    position: "absolute",
    left: "20%",
    right: "20%"
  },
  form: {
    color: "white"
  }
};

class Login extends Component {
  state = {
    email: "",
    password: "",
    isLogged: false,
    isUserGoogleLogged: false,
    isAdminLogged: false,
    isMounted: false,
    propsMessage: "",
    propsGoogleMessage: "",
    propsGoogleUserExistMessage: "",
    propsDelAccountMessage: "",
    show: true,
    childData: "",
    logging: false,
    activationComplete: false,

    googleForm: {
      androidUser: false,
      email: "",
      password: "",
      token: ""
    },

    errors: {
      loginMessage: "",
      passwordMessage: "",
      emailConfirmMessage: "",
      googleUserNotExist: "",
      googleAccount: ""
    }
  };

  componentDidMount() {
    this.setState({
      isMounted: true,
      propsMessage: this.props.location.state
        ? this.props.location.state.message
        : null,
      propsGoogleMessage: this.props.location.state
        ? this.props.location.state.messageGoogleUserAdded
        : null,
      propsGoogleUserExistMessage: this.props.location.state
        ? this.props.location.state.messageGoogleAccountExist
        : null,
      propsDelAccountMessage: this.props.delAccount.delAccount
    });
  }

  handleInputchange = e => {
    const name = e.target.name;
    const type = e.target.type;

    if (type === "email" || type === "password") {
      const value = e.target.value;
      this.state.isMounted &&
        this.setState({
          [name]: value
        });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.state.isMounted &&
      this.setState({
        activationComplete: true,
        logging: true
      });
    if (this.state.email === "admin@gamer.app") {
      fetch("https://gamerapps.herokuapp.com/login", {
        method: "post",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          email: this.state.email,
          password: this.state.password
        })
      })
        .then(response => {
          if (response) return response.json();
        })
        .then(data => {
          if (data) {
            this.state.isMounted && this.props.addUserDataToRedux(data);
            this.state.isMounted &&
              this.setState({
                isAdminLogged: true,
                logging: false,
                activationComplete: true
              });
          }
        })
        .catch(err => console.log(err));
    } else {
      fetch("https://gamerapps.herokuapp.com/login", {
        method: "post",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          email: this.state.email,
          password: this.state.password
        })
      })
        .then(response => {
          if (response) return response.json();
        })
        .then(data => {
          if (data.status === 1) {
            this.state.isMounted &&
              this.setState({
                logging: false,
                activationComplete: true,
                errors: {
                  loginMessage: data.error
                }
              });
          } else if (data.status === 2) {
            this.state.isMounted &&
              this.setState({
                logging: false,
                activationComplete: true,
                errors: {
                  emailConfirmMessage: data.error
                }
              });
          } else if (data.status === 3) {
            this.state.isMounted &&
              this.setState({
                logging: false,
                activationComplete: true,
                errors: {
                  passwordMessage: data.error
                }
              });
          } else if (data.status === 4) {
            this.state.isMounted &&
              this.setState({
                logging: false,
                activationComplete: true,
                errors: {
                  googleAccount: data.error
                }
              });
          } else {
            if (data) {
              this.state.isMounted && this.props.addUserDataToRedux(data);
              this.state.isMounted &&
                this.setState({
                  isLogged: true,
                  logging: false,
                  activationComplete: true
                });
            }
          }
        })
        .catch(err => console.log(err));
    }
  };

  handleDismiss = () => {
    this.state.isMounted && this.setState({ show: false });
    this.state.isMounted && this.props.history.replace("", null);
    this.state.isMounted && this.props.clearDelMessageFromRedux();
  };

  handleGoogleSuccessResponse = response => {
    this.state.isMounted &&
      this.setState({
        activationComplete: true,
        googleForm: {
          email: response.profileObj.email,
          password: "",
          token: response.tokenId
        }
      });
    fetch("https://gamerapps.herokuapp.com/googlelogin", {
      method: "post",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(this.state.googleForm)
    })
      .then(response => {
        if (response) return response.json();
      })
      .then(data => {
        if (data.status === 1) {
          this.state.isMounted &&
            this.setState({
              activationComplete: true,
              errors: {
                googleUserNotExist: data.error
              }
            });
        } else if (data.status === 2) {
          console.log(data.error);
        } else if (data) {
          this.state.isMounted && this.props.addUserDataToRedux(data);
          this.state.isMounted &&
            this.setState({
              isUserGoogleLogged: true,
              activationComplete: true
            });
        }
      })
      .catch(err => console.log(err));
  };

  handleGoogleFailureResponse = response => {
    console.log(response);
  };

  getData = data => {
    this.state.isMounted &&
      this.setState({
        childData: data,
        activationComplete: true
      });
  };

  componentWillUnmount() {
    this.setState({
      isMounted: false
    });
  }

  render() {
    return this.state.activationComplete &&
      (this.state.isLogged || this.state.isUserGoogleLogged) ? (
      <Redirect to="/profil" exact />
    ) : this.state.isAdminLogged ? (
      <Redirect to="/allaccounts" exact />
    ) : (
      <>
        {this.state.propsMessage ||
        this.state.propsGoogleMessage ||
        this.state.propsGoogleUserExistMessage ||
        this.state.errors.googleUserNotExist ||
        this.state.propsDelAccountMessage ||
        this.state.childData ? (
          <Delayed wait={800}>
            {this.state.show ? (
              <Alert
                style={{ textAlign: "center" }}
                variant="secondary"
                onClose={this.handleDismiss}
                dismissible
              >
                {this.state.propsMessage}
                {this.state.propsGoogleMessage}
                {this.state.propsGoogleUserExistMessage}
                {this.state.errors.googleUserNotExist}
                {this.state.propsDelAccountMessage}
                {this.state.childData}
              </Alert>
            ) : null}
          </Delayed>
        ) : null}
        <LoginForm
          errors={this.state.errors}
          submit={this.handleSubmit}
          change={this.handleInputchange}
          styles={styles}
          sendData={this.getData}
          disabledbutton={
            this.state.email === "" || this.state.password === "" ? 1 : 0
          }
          logging={this.state.logging ? 1 : 0}
        >
          <GoogleLogin
            clientId={clientId}
            buttonText="Se connecter avec Google"
            onSuccess={this.handleGoogleSuccessResponse}
            onFailure={this.handleGoogleFailureResponse}
            cookiePolicy={"single_host_origin"}
          />
        </LoginForm>
      </>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    addUserDataToRedux: data => dispatch(addUserData(data)),
    clearDelMessageFromRedux: () => dispatch(clearDelAccount())
  };
};

const mapStateToProps = state => {
  return {
    delAccount: state.delAccount
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Login);
