export const addUserData = data => {
  return {
    type: "addUserData",
    payload: data
  };
};

export const addGameStat = data => {
  return {
    type: "addGameStat",
    payload: data
  };
};

export const addDelAccount = data => {
  return {
    type: "addDelAccount",
    payload: data
  };
};

export const clearUserData = () => {
  return {
    type: "clearUserData"
  };
};

export const clearGameStat = () => {
  return {
    type: "clearGameStat"
  };
};

export const clearDelAccount = () => {
  return {
    type: "clearDelAccount"
  };
};
