import React, { Component } from "react";
import {
  Row,
  Col,
  Image,
  Button,
  ButtonToolbar,
  OverlayTrigger,
  Tooltip
} from "react-bootstrap";
import ModalUpdateProfil from "./ModalUpdateProfil";
import { addUserData, clearUserData, addDelAccount } from "../redux/action";
import { connect } from "react-redux";

class ProfilHeader extends Component {
  state = {
    isMounted: false,
    modalUpdateProfilShow: false,
    isGoogleAccount: false,
    modifing: false,
    deleting: false,
    photoSizeError: false,
    isTrashClicked: false,

    userData: {
      name: "",
      age: 0,
      sex: "",
      email: "",
      city: "",
      country: "",
      photo: "",
      googlePhoto: "",
      games: []
    },

    formUpdateProfile: {
      name: "",
      photo: "",
      age: 0,
      sex: "",
      city: "",
      country: ""
    }
  };

  componentDidMount() {
    this.setState({
      isMounted: true,
      userData: this.props.userData,
      isGoogleAccount: this.props.userData.isGoogleAccount
    });
  }

  handleChooseSubmit = (action, decision) => {
    if (action === "changeProfil") {
      this.handleSubmitProfilChange();
    } else if (action === "trashClicked") {
      this.handleTrashClicked();
    } else if (action === "deleteAccount") {
      this.handleSubmitDeleteAccount(decision);
    }
  };

  handleTrashClicked = () => {
    this.state.isMounted &&
      this.setState({
        isTrashClicked: true
      });
  };

  handleChooseOnChange = (action, e, picture) => {
    if (action === "photo") {
      this.onDrop(picture);
    } else if (action === "other") {
      this.handleUpdateProfilChange(e);
    }
  };

  handleUpdateProfilChange = e => {
    this.state.isMounted &&
      this.setState({
        formUpdateProfile: {
          ...this.state.formUpdateProfile,
          [e.target.name]: this.capitalize(e.target.value)
        }
      });
  };

  onDrop = picture => {
    if (picture.length > 1) {
      picture = picture.splice(picture.length - 1, 1);
    }
    if (picture[0].size > 71659) {
      this.state.isMounted &&
        this.setState({
          photoSizeError: true
        });
    } else {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(picture[0]);
      fileReader.onloadend = () => {
        this.state.isMounted &&
          this.setState({
            photoSizeError: false,
            formUpdateProfile: {
              ...this.state.formUpdateProfile,
              photo: fileReader.result
            }
          });
      };
    }
  };

  handleSubmitDeleteAccount = decision => {
    if (decision === "yes") {
      this.state.isMounted &&
        this.setState({
          deleting: true
        });
      fetch("https://gamerapps.herokuapp.com/deleteaccount", {
        method: "post",
        headers: this.props.userData.token
          ? {
              "x-auth": this.props.userData.token,
              "Content-Type": "application/json"
            }
          : {
              "Content-Type": "application/json"
            },
        body: JSON.stringify({
          email: this.props.userData.email,
          isGoogleAccount: this.state.isGoogleAccount
        })
      })
        .then(response => {
          if (response) return response.json();
        })
        .then(data => {
          if (data.success) {
            this.state.isMounted &&
              this.setState({
                deleting: false
              });
            this.state.isMounted &&
              this.props.addDelAccountToRedux(data.success);
            this.state.isMounted && this.props.clearUserDataFromRedux();
          }
        })
        .catch(err => console.log(err));
    } else if (decision === "no") {
      this.state.isMounted &&
        this.setState({
          isTrashClicked: false
        });
    }
  };

  handleSubmitProfilChange = () => {
    this.state.isMounted &&
      this.setState({
        modifing: true
      });
    fetch("https://gamerapps.herokuapp.com/updateprofile", {
      method: "post",
      headers: this.props.userData.token
        ? {
            "x-auth": this.props.userData.token,
            "Content-Type": "application/json"
          }
        : {
            "Content-Type": "application/json"
          },
      body: JSON.stringify({
        email: this.props.userData.email,
        isGoogleAccount: this.state.isGoogleAccount,
        formUpdateProfile: this.state.formUpdateProfile
      })
    })
      .then(response => {
        if (response) return response.json();
      })
      .then(data => {
        if (data.success) {
          this.state.isMounted && this.props.addUserDataToRedux(data.success);
          this.state.isMounted &&
            this.setState({
              userData: data.success,
              modifing: false,
              modalUpdateProfilShow: false,
              formUpdateProfile: {
                name: "",
                photo: "",
                age: 0,
                sex: "",
                city: "",
                country: ""
              }
            });
        }
      })
      .catch(err => console.log(err));
  };

  capitalize = text => {
    return text
      .toLowerCase()
      .split(" ")
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join(" ");
  };

  componentWillUnmount() {
    this.setState({
      isMounted: false
    });
  }
  render() {
    const { name, sex, city, country, age } = this.state.userData;
    const { userDataStyle, photo, containerHeader } = this.props.styles;
    return (
      <div style={containerHeader}>
        <Row>
          <Col md={9} />
          <Col md={2}>
            <div
              style={{
                opacity: "0.9",
                width: "42px",
                marginBottom: "2%",
                borderRadius: "50%"
              }}
            >
              <ButtonToolbar>
                <OverlayTrigger
                  key={"right"}
                  placement={"right"}
                  overlay={
                    <Tooltip id={`tooltip-right`}>
                      Modifier votre profil
                    </Tooltip>
                  }
                >
                  <Button
                    style={{ borderRadius: "65%" }}
                    variant="outline-primary"
                    onClick={e => {
                      e.preventDefault();
                      this.state.isMounted &&
                        this.setState({
                          modalUpdateProfilShow: true,
                          formUpdateProfile: {
                            name: this.state.userData.name
                              ? this.state.userData.name
                              : "",
                            photo: this.state.userData.photo
                              ? this.state.userData.photo
                              : "",
                            age: this.state.userData.age
                              ? this.state.userData.age
                              : 0,
                            sex: this.state.userData.sex
                              ? this.state.userData.sex
                              : "",
                            city: this.state.userData.city
                              ? this.state.userData.city
                              : "",
                            country: this.state.userData.country
                              ? this.state.userData.country
                              : ""
                          }
                        });
                    }}
                  >
                    <i className="fas fa-pen-square" />
                  </Button>
                </OverlayTrigger>
              </ButtonToolbar>
              <ModalUpdateProfil
                show={this.state.modalUpdateProfilShow}
                onHide={() => {
                  this.state.isMounted &&
                    this.setState({
                      modalUpdateProfilShow: false,
                      photoSizeError: false,
                      isTrashClicked: false,
                      formUpdateProfile: {
                        name: "",
                        photo: "",
                        age: 0,
                        sex: "",
                        city: "",
                        country: ""
                      }
                    });
                }}
                isgoogleaccount={this.state.isGoogleAccount ? 1 : 0}
                onChange={this.handleChooseOnChange}
                onSubmit={this.handleChooseSubmit}
                modifing={this.state.modifing ? 1 : 0}
                deleting={this.state.deleting ? 1 : 0}
                userdata={this.state.userData}
                photo={this.state.formUpdateProfile.photo}
                error={this.state.photoSizeError ? 1 : 0}
                istrashclicked={this.state.isTrashClicked ? 1 : 0}
              />
            </div>
            <div style={photo}>
              <Image
                style={{
                  margin: "auto",
                  border: "2px solid crimson",
                  objectFit: "cover"
                }}
                src={
                  this.state.userData.photo
                    ? this.state.userData.photo
                    : require("../img/userDefaultPhoto.png") &&
                      this.state.userData.googlePhoto
                    ? this.state.userData.googlePhoto
                    : require("../img/userDefaultPhoto.png")
                }
                width="90px"
                height="90px"
                roundedCircle
              />
            </div>
            <div style={photo}>
              <p style={userDataStyle}>{name}</p>
              <p />
              {age !== undefined && age !== null && age !== 0 && (
                <p style={userDataStyle}>{`${age} ans`}</p>
              )}
              <p style={userDataStyle}>{sex}</p>
              <p style={userDataStyle}>{city}</p>
              <p style={userDataStyle}>{country}</p>
            </div>
          </Col>
          <Col md={1} />
        </Row>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    userData: state.userData
  };
};

const mapDispatchToProps = dispatch => {
  return {
    addUserDataToRedux: data => dispatch(addUserData(data)),
    clearUserDataFromRedux: () => dispatch(clearUserData()),
    addDelAccountToRedux: data => dispatch(addDelAccount(data))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ProfilHeader);
