import React from "react";
import {
  Modal,
  Button,
  Form,
  Row,
  Col,
  ButtonGroup,
  OverlayTrigger,
  Tooltip,
  Spinner
} from "react-bootstrap";

const ModalGameModif = props => {
  const {
    onSubmit,
    onChange,
    onHide,
    gametomodif,
    disabledbutton,
    modifing,
    deleting,
    error,
    errormission,
    totalmissions
  } = props;
  return (
    <>
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header
          style={{ backgroundColor: "#00222A", color: "white" }}
          closeButton={false}
        >
          <Modal.Title
            style={{ margin: "0 auto" }}
            id="contained-modal-title-vcenter"
          >
            {gametomodif}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ backgroundColor: "#00222A", color: "white" }}>
          <Form noValidate>
            <Row>
              <Col md={4} />
              <Col md={4}>
                <Form.Group controlId="formBasicNewMission">
                  <Form.Label>Nouvelle Mission Actuelle</Form.Label>
                  <Form.Control
                    onChange={onChange}
                    name="newMission"
                    type="number"
                    placeholder="nouvelle mission actuelle"
                  />
                  <span style={{ color: "#75FFD6" }}>
                    Totalité des missions : {totalmissions}
                  </span>
                  {error === 1 ? (
                    <p style={{ color: "#F0DC4E" }}>
                      Mission actuelle supérieure à la totalité des missions.
                    </p>
                  ) : null}
                  {errormission === 1 ? (
                    <p style={{ color: "#F0DC4E" }}>
                      Mission inférieure à la dernière enregistrée.
                    </p>
                  ) : null}
                </Form.Group>
              </Col>
              <Col md={4} />
            </Row>
          </Form>
        </Modal.Body>
        <Modal.Footer
          style={{
            display: "flex",
            justifyContent: "space-between",
            backgroundColor: "#00222A",
            color: "white"
          }}
        >
          <ButtonGroup>
            <OverlayTrigger
              key={"right-trash"}
              placement={"right"}
              overlay={<Tooltip id={"tooltip-right"}>Supprimer ce jeu</Tooltip>}
            >
              <Button
                disabled={deleting === 1 ? true : false}
                className="mr-3"
                style={{
                  borderRadius: "50%"
                }}
                variant="outline-primary"
                onClick={e => {
                  e.preventDefault();
                  onSubmit("trash");
                }}
              >
                <i className="fas fa-trash" />
              </Button>
            </OverlayTrigger>
            <OverlayTrigger
              key={"right-stats"}
              placement={"right"}
              overlay={
                <Tooltip id={"tooltip-right"}>
                  Afficher des statistiques
                </Tooltip>
              }
            >
              <Button
                style={{ borderRadius: "50%" }}
                variant="outline-primary"
                onClick={e => {
                  e.preventDefault();
                  onSubmit("stats");
                }}
              >
                <i className="fas fa-chart-line" />
              </Button>
            </OverlayTrigger>
          </ButtonGroup>
          <ButtonGroup>
            <Button
              className="mr-5"
              onClick={e => {
                e.preventDefault();
                onSubmit("modifMission");
              }}
              variant="success"
              disabled={modifing === 1 || disabledbutton === 1 ? true : false}
            >
              {modifing === 1 && (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              )}
              {modifing === 1 ? "Je modifie..." : "Modifier"}
            </Button>
            <Button variant="info" onClick={onHide}>
              Annuler
            </Button>
          </ButtonGroup>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default ModalGameModif;
