import React, { Component } from "react";
import SignUpForm from "../component/SignUpForm";
import { Redirect } from "react-router-dom";
import { Alert } from "react-bootstrap";
import Delayed from "../component/Delayed";
import { GoogleLogin } from "react-google-login";
import { clientId } from "../config/security";

const styles = {
  container: {
    top: "20%",
    position: "absolute",
    left: "20%",
    right: "20%"
  },
  form: {
    color: "white"
  }
};

class SignUp extends Component {
  state = {
    name: "",
    email: "",
    emailConfirm: "",
    password: "",
    isMounted: false,
    emailError: "",
    saveFormError: "",
    userAdded: false,
    show: true,
    emailConfirmSent: false,
    googleUserAdded: false,
    googleAccountExistMessage: "",
    saving: false,

    formErrors: {
      userNameShort: "",
      userNameSpace: "",
      emailIncorrect: "",
      passwordIncorrect: "",
      userEmailExist: ""
    },

    googleForm: {
      name: "",
      email: "",
      photo: ""
    }
  };

  componentDidMount() {
    this.setState({
      isMounted: true
    });
  }

  handleSubmit = e => {
    e.preventDefault();
    this.state.isMounted &&
      this.setState({
        saving: true
      });
    const emailMatch = this.state.email === this.state.emailConfirm;
    if (!emailMatch) {
      this.state.isMounted &&
        this.setState({
          emailError: "Les mails saisis sont differents"
        });
    } else {
      fetch("https://gamerapps.herokuapp.com/signup", {
        method: "post",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          user: {
            name: this.state.name,
            email: this.state.email,
            password: this.state.password
          }
        })
      })
        .then(response => {
          if (response) return response.json();
        })
        .then(data => {
          if (data.success) {
            console.log(data.success);
            this.state.isMounted &&
              this.setState({
                userAdded: true
              });
            fetch("https://gamerapps.herokuapp.com/emailconfirm", {
              method: "post",
              headers: {
                "Content-Type": "application/json"
              },
              body: JSON.stringify({ email: this.state.email, token: "" })
            })
              .then(response => {
                if (response) return response.json();
              })
              .then(data => {
                if (data.info) {
                  console.log(data.info);
                  this.state.isMounted &&
                    this.setState({
                      emailConfirmSent: true,
                      saving: false
                    });
                }
              })
              .catch(err => console.log(err));
          } else if (data.status === 400) {
            this.state.isMounted &&
              this.setState({
                saveFormError: data.error,
                saving: false
              });
          } else if (data.status === 1) {
            this.state.isMounted &&
              this.setState({
                saving: false,
                formErrors: {
                  userNameShort: data.error
                }
              });
          } else if (data.status === 2) {
            this.state.isMounted &&
              this.setState({
                saving: false,
                formErrors: {
                  userNameSpace: data.error
                }
              });
          } else if (data.status === 3) {
            this.state.isMounted &&
              this.setState({
                saving: false,
                formErrors: {
                  emailIncorrect: data.error
                }
              });
          } else if (data.status === 4) {
            this.state.isMounted &&
              this.setState({
                saving: false,
                formErrors: {
                  passwordIncorrect: data.error
                }
              });
          } else if (data.status === 5) {
            this.state.isMounted &&
              this.setState({
                saving: false,
                formErrors: {
                  userEmailExist: data.error
                }
              });
          }
        })
        .catch(err => console.log(err));
    }
  };

  handleInputchange = e => {
    const name = e.target.name;
    const type = e.target.type;

    if (type === "text" || type === "email" || type === "password") {
      const value = e.target.value;

      this.state.isMounted &&
        this.setState({
          [name]: value
        });
    }
  };

  handleDismiss = () => {
    this.state.isMounted && this.setState({ show: false });
  };

  handleGoogleSuccessResponse = response => {
    this.state.isMounted &&
      this.setState({
        googleForm: {
          name: response.profileObj.name,
          email: response.profileObj.email,
          photo: response.profileObj.imageUrl
        }
      });

    fetch("https://gamerapps.herokuapp.com/googlesignup", {
      method: "post",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        user: {
          name: this.state.googleForm.name,
          email: this.state.googleForm.email,
          googlePhoto: this.state.googleForm.photo
        }
      })
    })
      .then(response => {
        if (response) return response.json();
      })
      .then(data => {
        if (data.success) {
          console.log(data.success);
          this.state.isMounted &&
            this.setState({
              googleUserAdded: true
            });
        } else if (data.status === 1) {
          this.state.isMounted &&
            this.setState({
              googleAccountExistMessage: data.error
            });
        }
      });
  };

  handleGoogleFailureResponse = response => {
    console.log(response);
  };

  componentWillUnmount() {
    this.setState({
      isMounted: false
    });
  }
  render() {
    return (this.state.userAdded && this.state.emailConfirmSent) ||
      this.state.googleUserAdded ||
      this.state.googleAccountExistMessage ? (
      <Redirect
        to={{
          pathname: "/",
          state: {
            message:
              this.state.userAdded && this.state.emailConfirmSent
                ? "Votre compte a été créé. Vous allez recevoir un mail de confirmation. Confirmez votre adresse et connectez-vous"
                : null,
            messageGoogleUserAdded: this.state.googleUserAdded
              ? "Votre compte a été créé. Connectez-vous en cliquant sur le bouton Google se connecter."
              : null,
            messageGoogleAccountExist: this.state.googleAccountExistMessage
              ? this.state.googleAccountExistMessage
              : null
          }
        }}
        exact
      />
    ) : (
      <>
        {this.state.saveFormError ? (
          <Delayed wait={800}>
            {this.state.show ? (
              <Alert
                style={{ textAlign: "center" }}
                variant="danger"
                onClose={this.handleDismiss}
                dismissible
              >
                {this.state.saveFormError}
              </Alert>
            ) : null}
          </Delayed>
        ) : null}
        <SignUpForm
          formErrors={this.state.formErrors}
          error={this.state.emailError}
          submit={this.handleSubmit}
          change={this.handleInputchange}
          styles={styles}
          saving={this.state.saving ? 1 : 0}
          disabledbutton={
            this.state.name === "" ||
            this.state.email === "" ||
            this.state.emailConfirm === "" ||
            this.state.password === ""
              ? 1
              : 0
          }
        >
          <GoogleLogin
            clientId={clientId}
            buttonText="S'inscrire avec Google"
            onSuccess={this.handleGoogleSuccessResponse}
            onFailure={this.handleGoogleFailureResponse}
            cookiePolicy={"single_host_origin"}
          />
        </SignUpForm>
      </>
    );
  }
}

export default SignUp;
