import React, { Component } from "react";
import { Form, Col, Row, Button, Spinner, Nav } from "react-bootstrap";
import ModalPassRemind from "./ModalPassRemind";

class LoginForm extends Component {
  state = {
    modalShow: false,
    isMounted: false,
    email: "",
    emailNotExist: false,
    emailIncorrect: false,
    passSent: false,
    sending: false,
    googleAccount: false
  };

  componentDidMount() {
    this.setState({
      isMounted: true
    });
  }

  handleInputChange = e => {
    const name = e.target.name;
    const type = e.target.type;

    if (type === "email") {
      const value = e.target.value;
      this.state.isMounted &&
        this.setState({
          [name]: value === "admin@gamer.app" ? "" : value
        });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.state.isMounted &&
      this.setState({
        sending: true
      });
    fetch("https://gamerapps.herokuapp.com/passremind", {
      method: "post",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        email: this.state.email
      })
    })
      .then(response => {
        if (response) return response.json();
      })
      .then(data => {
        if (data.success) {
          this.state.isMounted &&
            this.setState({
              passSent: true
            });
          this.props.sendData(data.success);
          if (this.state.passSent) {
            this.state.isMounted &&
              this.setState({
                modalShow: false,
                email: "",
                passSent: false,
                emailNotExist: false,
                emailIncorrect: false,
                googleAccount: false,
                sending: false
              });
          }
        } else if (data.status === 1) {
          this.state.isMounted &&
            this.setState({
              emailIncorrect: true
            });
          if (this.state.emailIncorrect) {
            this.state.isMounted &&
              this.setState({
                modalShow: true,
                emailNotExist: false,
                googleAccount: false,
                sending: false
              });
          } else {
            this.state.isMounted &&
              this.setState({
                modalShow: false
              });
          }
        } else if (data.status === 2) {
          this.state.isMounted &&
            this.setState({
              emailNotExist: true
            });
          if (this.state.emailNotExist) {
            this.state.isMounted &&
              this.setState({
                modalShow: true,
                emailIncorrect: false,
                googleAccount: false,
                sending: false
              });
          } else {
            this.state.isMounted &&
              this.setState({
                modalShow: false
              });
          }
        } else if (data.status === 3) {
          this.state.isMounted &&
            this.setState({
              googleAccount: true
            });
          if (this.state.googleAccount) {
            this.state.isMounted &&
              this.setState({
                modalShow: true,
                emailIncorrect: false,
                emailNotExist: false,
                sending: false
              });
          } else {
            this.state.isMounted &&
              this.setState({
                modalShow: false
              });
          }
        }
      })
      .catch(err => console.log(err));
  };

  componentWillUnmount() {
    this.setState({
      isMounted: false
    });
  }

  render() {
    const { container, form } = this.props.styles;
    const { submit, change, children, logging, disabledbutton } = this.props;
    const {
      loginMessage,
      passwordMessage,
      emailConfirmMessage,
      googleAccount
    } = this.props.errors;
    return (
      <div style={container}>
        <Form onSubmit={submit}>
          <Row>
            <Col md={4} />
            <Col md={4}>
              <Form.Group controlId="formBasicEmail">
                <Form.Label style={form}>Email</Form.Label>
                <Form.Control
                  onChange={change}
                  name="email"
                  type="email"
                  placeholder="email"
                />
                {loginMessage ? (
                  <p style={{ color: "#f0ad4e" }}>{loginMessage}</p>
                ) : null}
                {emailConfirmMessage ? (
                  <p style={{ color: "#f0ad4e" }}>{emailConfirmMessage}</p>
                ) : null}
                {googleAccount ? (
                  <p style={{ color: "#f0ad4e" }}>{googleAccount}</p>
                ) : null}
              </Form.Group>
            </Col>
            <Col md={4} />
          </Row>
          <Row>
            <Col md={4} />
            <Col md={4}>
              <Form.Group controlId="formBasicPassword">
                <Form.Label style={form}>Mot de passe</Form.Label>
                <Form.Control
                  onChange={change}
                  name="password"
                  type="password"
                  placeholder="mot de passe"
                />
                {passwordMessage ? (
                  <p style={{ color: "#f0ad4e" }}>{passwordMessage}</p>
                ) : null}
              </Form.Group>
            </Col>
            <Col md={4} />
          </Row>
          <Row>
            <Col md={4} />
            <Col md={5}>
              <Button
                style={{ display: "inline-block" }}
                variant="primary"
                type="submit"
                disabled={logging === 1 || disabledbutton === 1 ? true : false}
              >
                {this.props.logging === 1 && (
                  <Spinner
                    as="span"
                    animation="border"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                  />
                )}
                {this.props.logging === 1 ? "Je connecte..." : "Se connecter"}
              </Button>
              <Nav.Link
                style={{ display: "inline-block", marginLeft: "18%" }}
                onClick={e => {
                  e.preventDefault();
                  this.state.isMounted &&
                    this.setState({
                      modalShow: true
                    });
                }}
              >
                Mot de passe oublié ?
              </Nav.Link>
              <ModalPassRemind
                errors={{
                  emailNotExist: this.state.emailNotExist ? 1 : 0,
                  emailIncorrect: this.state.emailIncorrect ? 1 : 0,
                  googleAccount: this.state.googleAccount ? 1 : 0
                }}
                sending={this.state.sending ? 1 : 0}
                show={this.state.modalShow}
                disabledbutton={this.state.email === "" ? 1 : 0}
                onSubmit={this.handleSubmit}
                onChange={this.handleInputChange}
                onHide={() => {
                  this.state.isMounted &&
                    this.setState({
                      modalShow: false,
                      email: "",
                      passSent: false,
                      emailNotExist: false,
                      emailIncorrect: false,
                      googleAccount: false
                    });
                }}
              />
            </Col>
            <Col md={3} />
          </Row>
          <Row style={{ marginTop: "5%" }}>
            <Col md={4} />
            <Col md={4}>{children}</Col>
            <Col md={4} />
          </Row>
        </Form>
      </div>
    );
  }
}

export default LoginForm;
