import React from "react";
import { Form, Col, Row, Button, Spinner } from "react-bootstrap";

const SignUpForm = props => {
  const { container, form } = props.styles;
  const { submit, change, children, saving, disabledbutton } = props;
  const emailError = props.error;
  const {
    userNameShort,
    userNameSpace,
    emailIncorrect,
    passwordIncorrect,
    userEmailExist
  } = props.formErrors;
  return (
    <div style={container}>
      <Form onSubmit={submit} noValidate>
        <Row>
          <Col md={4} />
          <Col md={4}>
            <Form.Group controlId="formBasicName">
              <Form.Label style={form}>Nom d'utilisateur</Form.Label>
              <Form.Control
                onChange={change}
                name="name"
                type="text"
                placeholder="nom d'utilisateur"
              />
              {userNameShort ? (
                <p style={{ color: "#f0ad4e" }}>{userNameShort}</p>
              ) : (
                ""
              )}
              {userNameSpace ? (
                <p style={{ color: "#f0ad4e" }}>{userNameSpace}</p>
              ) : (
                ""
              )}
            </Form.Group>
          </Col>
          <Col md={4} />
        </Row>
        <Row>
          <Col md={4} />
          <Col md={4}>
            <Form.Group controlId="formBasicEmail">
              <Form.Label style={form}>Email</Form.Label>
              <Form.Control
                onChange={change}
                name="email"
                type="email"
                placeholder="email"
              />
              {emailIncorrect ? (
                <p style={{ color: "#f0ad4e" }}>{emailIncorrect}</p>
              ) : (
                ""
              )}
              {userEmailExist ? (
                <p style={{ color: "#f0ad4e" }}>{userEmailExist}</p>
              ) : (
                ""
              )}
            </Form.Group>
          </Col>
          <Col md={4} />
        </Row>
        <Row>
          <Col md={4} />
          <Col md={4}>
            <Form.Group controlId="formBasicConfirmEmail">
              <Form.Label style={form}>Confirmer Email</Form.Label>
              <Form.Control
                onChange={change}
                name="emailConfirm"
                type="email"
                placeholder="confirmer email"
              />
              {emailError ? (
                <p style={{ color: "#f0ad4e" }}>{emailError}</p>
              ) : (
                ""
              )}
            </Form.Group>
          </Col>
          <Col md={4} />
        </Row>
        <Row>
          <Col md={4} />
          <Col md={4}>
            <Form.Group controlId="formBasicPassword">
              <Form.Label style={form}>Mot de passe</Form.Label>
              <Form.Control
                onChange={change}
                name="password"
                type="password"
                placeholder="mot de passe"
              />
              {passwordIncorrect ? (
                <p style={{ color: "#f0ad4e", whiteSpace: "pre-wrap" }}>
                  {passwordIncorrect}
                </p>
              ) : (
                ""
              )}
            </Form.Group>
          </Col>
          <Col md={4} />
        </Row>
        <Row>
          <Col md={4} />
          <Col md={3}>
            <Button
              variant="primary"
              type="submit"
              disabled={saving === 1 || disabledbutton ? true : false}
            >
              {saving === 1 && (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              )}
              {saving === 1 ? "J'enregistre..." : "Enregistrer"}
            </Button>
          </Col>
          <Col md={5} />
        </Row>
      </Form>
      <Row style={{ marginTop: "5%" }}>
        <Col md={4} />
        <Col md={4}>{children}</Col>
        <Col md={4} />
      </Row>
    </div>
  );
};

export default SignUpForm;
