import React from "react";
import {
  Modal,
  Button,
  Form,
  Row,
  Col,
  Spinner,
  Image,
  OverlayTrigger,
  Tooltip,
  ButtonGroup
} from "react-bootstrap";
import ImageUploader from "react-images-upload";

const ModalUpdateProfil = props => {
  const {
    onSubmit,
    onChange,
    onHide,
    modifing,
    deleting,
    isgoogleaccount,
    photo,
    error,
    istrashclicked
  } = props;
  const { name, sex, age, city, country } = props.userdata;
  return (
    <>
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header
          style={{ backgroundColor: "#00222A", color: "white" }}
          closeButton={false}
        >
          <Modal.Title
            style={{ margin: "0 auto" }}
            id="contained-modal-title-vcenter"
          >
            Modifier votre profil
          </Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ backgroundColor: "#00222A", color: "white" }}>
          <Form noValidate>
            {isgoogleaccount === 0 && (
              <Row>
                <Col md={4} />
                <Col md={4}>
                  <Form.Group controlId="formBasicNewName">
                    <Form.Label>Votre nom</Form.Label>
                    <Form.Control
                      onChange={e => {
                        onChange("other", e, null);
                      }}
                      name="name"
                      type="text"
                      placeholder="votre nom"
                      defaultValue={name}
                    />
                  </Form.Group>
                </Col>
                <Col md={4} />
              </Row>
            )}
            {isgoogleaccount === 0 && (
              <Row>
                <Col md={4} />
                <Col md={4}>
                  <Form.Group controlId="formBasicNewPhoto">
                    <Form.Label>Votre photo</Form.Label>
                    <ImageUploader
                      fileContainerStyle={{ height: "60px" }}
                      withIcon={false}
                      singleImage={true}
                      buttonText="Choisissez votre photo"
                      onChange={picture => {
                        onChange("photo", null, picture);
                      }}
                      imgExtension={[".jpg", "jpeg", ".gif", ".png", ".gif"]}
                      fileSizeError="Photo trop large."
                      maxFileSize={500000}
                    />
                    {photo && (
                      <Image
                        style={{
                          objectFit: "cover",
                          border: "1px solid crimson"
                        }}
                        width="90px"
                        height="90px"
                        src={photo}
                        rounded
                      />
                    )}
                    {error === 1 ? (
                      <p style={{ color: "#F0DC4E" }}>
                        Photo trop lourde. Max 70kB est acceptée.
                      </p>
                    ) : null}
                  </Form.Group>
                </Col>
                <Col md={4} />
              </Row>
            )}
            <Row>
              <Col md={4} />
              <Col md={4}>
                <Form.Group controlId="formBasicNewAge">
                  <Form.Label>Votre âge</Form.Label>
                  <Form.Control
                    onChange={e => {
                      onChange("other", e, null);
                    }}
                    name="age"
                    type="number"
                    placeholder="votre âge"
                    defaultValue={age}
                  />
                </Form.Group>
              </Col>
              <Col md={4} />
            </Row>
            <Row>
              <Col md={4} />
              <Col md={4}>
                <Form.Group controlId="formBasicNewSex">
                  <Form.Label>Votre sexe</Form.Label>
                  <Form.Check
                    onChange={e => {
                      onChange("other", e, null);
                    }}
                    name="sex"
                    type="radio"
                    value="Homme"
                    label="Homme"
                    id="Homme"
                    defaultChecked={sex === "Homme" ? "Homme" : null}
                  />
                  <Form.Check
                    onChange={e => {
                      onChange("other", e, null);
                    }}
                    name="sex"
                    type="radio"
                    value="Femme"
                    label="Femme"
                    id="Femme"
                    defaultChecked={sex === "Femme" ? "Femme" : null}
                  />
                </Form.Group>
              </Col>
              <Col md={4} />
            </Row>
            <Row>
              <Col md={4} />
              <Col md={4}>
                <Form.Group controlId="formBasicNewCity">
                  <Form.Label>Votre ville</Form.Label>
                  <Form.Control
                    onChange={e => {
                      onChange("other", e, null);
                    }}
                    name="city"
                    type="text"
                    placeholder="votre ville"
                    defaultValue={city}
                  />
                </Form.Group>
              </Col>
              <Col md={4} />
            </Row>
            <Row>
              <Col md={4} />
              <Col md={4}>
                <Form.Group controlId="formBasicNewCountry">
                  <Form.Label>Votre pays</Form.Label>
                  <Form.Control
                    onChange={e => {
                      onChange("other", e, null);
                    }}
                    name="country"
                    type="text"
                    placeholder="votre pays"
                    defaultValue={country}
                  />
                </Form.Group>
              </Col>
              <Col md={4} />
            </Row>
          </Form>
        </Modal.Body>
        <Modal.Footer
          style={{
            backgroundColor: "#00222A",
            color: "white",
            display: "flex",
            justifyContent: "space-between"
          }}
        >
          {istrashclicked === 1 ? (
            <div>
              <p style={{ color: "#FF4B3A" }}>Vous êtes sûr ?</p>
              <ButtonGroup>
                <Button
                  disabled={deleting === 1 ? true : false}
                  className="mr-3"
                  style={{
                    borderRadius: "50%"
                  }}
                  variant="outline-primary"
                  onClick={e => {
                    e.preventDefault();
                    onSubmit("deleteAccount", "yes");
                  }}
                >
                  <i className="fas fa-check" />
                </Button>
                <Button
                  disabled={deleting === 1 ? true : false}
                  style={{
                    borderRadius: "50%"
                  }}
                  variant="outline-primary"
                  onClick={e => {
                    e.preventDefault();
                    onSubmit("deleteAccount", "no");
                  }}
                >
                  <i className="fas fa-times" />
                </Button>
              </ButtonGroup>
            </div>
          ) : (
            <ButtonGroup>
              <OverlayTrigger
                key={"right-trash"}
                placement={"right"}
                overlay={
                  <Tooltip id={"tooltip-right"}>Supprimer votre compte</Tooltip>
                }
              >
                <Button
                  className="mr-3"
                  style={{
                    borderRadius: "50%"
                  }}
                  variant="outline-primary"
                  onClick={e => {
                    e.preventDefault();
                    onSubmit("trashClicked", null);
                  }}
                >
                  <i className="fas fa-trash" />
                </Button>
              </OverlayTrigger>
            </ButtonGroup>
          )}
          <ButtonGroup>
            <Button
              className="mr-5"
              variant="success"
              onClick={e => {
                e.preventDefault();
                onSubmit("changeProfil", null);
              }}
              disabled={modifing === 1 || error === 1 ? true : false}
            >
              {modifing === 1 && (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              )}
              {modifing === 1 ? "Je modifie..." : "Modifier"}
            </Button>
            <Button variant="info" onClick={onHide}>
              Annuler
            </Button>
          </ButtonGroup>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default ModalUpdateProfil;
