import React from "react";
import "../styles/Profil.css";
import ProfilHeader from "../component/ProfilHeader";
import ProfilGames from "../component/ProfilGames";
import ProfilGameProgress from "../component/ProfilGameProgress";

const style = {
  photo: {
    width: "150px",
    backgroundColor: "#000000",
    textAlign: "center",
    opacity: "0.7",
    borderRadius: "5px",
    marginBottom: "2%"
  },
  userDataStyle: {
    color: "#02CCD5",
    fontSize: "15px",
    textAlign: "center",
    marginBottom: "1%",
    opacity: "0.7"
  },
  containerHeader: {
    position: "absolute",
    top: "8%",
    left: "5%",
    right: "5%"
  },
  containerGames: {
    position: "absolute",
    top: "10%",
    left: "10%",
    right: "10%"
  },
  containerProgress: {
    marginLeft: "auto",
    marginRight: "auto",
    marginTop: "32%"
  },
  buttonAdd: {
    borderRadius: "50%"
  },
  listItem: {
    backgroundColor: "#00222A",
    textAlign: "justify",
    color: "#01EBE2",
    fontSize: "14px",
    height: "50px"
  },
  noGames: {
    width: "220px",
    height: "50px",
    borderRadius: "5px",
    textAlign: "center",
    lineHeight: "50px",
    backgroundColor: "#00222A",
    color: "white",
    marginTop: "4%",
    marginLeft: "1%"
  }
};

const Profil = () => {
  return (
    <>
      <ProfilHeader styles={style} />
      <ProfilGames styless={style} />
      <ProfilGameProgress stylesss={style} />
    </>
  );
};

export default Profil;
