import React, { Component } from "react";
import {
  Row,
  Col,
  Container,
  ListGroup,
  Image,
  OverlayTrigger,
  Button,
  Tooltip
} from "react-bootstrap";
import SimpleBar from "simplebar-react";
import "simplebar/dist/simplebar.min.css";
import { connect } from "react-redux";

class NotConfirmedAccounts extends Component {
  state = {
    loading: false,
    notConfirmed: []
  };

  componentDidMount() {
    this.setState({
      loading: true
    });
    fetch("https://gamerapps.herokuapp.com/notconfirmed", {
      method: "post",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        email: this.props.userData.email
      })
    })
      .then(response => {
        if (response) return response.json();
      })
      .then(data => {
        if (data) {
          this.setState({
            loading: false,
            notConfirmed: data
          });
        }
      })
      .catch(err => console.log(err));
  }

  render() {
    const name = this.props.userData.name;
    let list = this.state.notConfirmed.map((item, i) => {
      return this.state.loading ? (
        <p
          style={{
            position: "absolute",
            top: "25%",
            left: "15%",
            width: "220px",
            height: "50px",
            borderRadius: "5px",
            textAlign: "center",
            lineHeight: "50px",
            backgroundColor: "#00222A",
            color: "#C3E895",
            opacity: "0.9"
          }}
        >
          En cours de chargement ...
        </p>
      ) : (
        <div key={i}>
          {item.name !== name && (
            <ListGroup.Item
              style={{
                backgroundColor: "#00222A",
                textAlign: "justify",
                color: "#FFB49E",
                fontSize: "14px",
                height: "50px"
              }}
            >
              <Row>
                <Col md={4}>
                  <p>{item.name}</p>
                </Col>
                <Col md={6}>
                  <p>{item.email}</p>
                </Col>
                <Col md={2}>
                  <p>
                    {new Date(item.creation_date).getDate() > 1 &&
                    new Date(item.creation_date).getDate() < 10
                      ? `0${new Date(item.creation_date).getDate()}`
                      : new Date(item.creation_date).getDate()}
                    /
                    {new Date(item.creation_date).getMonth() + 1 > 1 &&
                    new Date(item.creation_date).getMonth() + 1 < 10
                      ? `0${new Date(item.creation_date).getMonth() + 1}`
                      : new Date(item.creation_date).getMonth() + 1}
                    /{new Date(item.creation_date).getFullYear()}
                  </p>
                </Col>
              </Row>
            </ListGroup.Item>
          )}
        </div>
      );
    });
    return (
      <div
        style={{
          position: "absolute",
          top: "15%",
          left: "5%",
          right: "5%",
          bottom: "5%"
        }}
      >
        <Container>
          <Row>
            <Col md={2} />
            <Col md={7}>
              {this.state.notConfirmed.length === 0 ? (
                <p
                  style={{
                    position: "absolute",
                    top: "15%",
                    left: "5%",
                    width: "220px",
                    height: "50px",
                    borderRadius: "5px",
                    textAlign: "center",
                    lineHeight: "50px",
                    backgroundColor: "#00222A",
                    color: "#C3E895",
                    opacity: "0.9"
                  }}
                >
                  Pas de non confirmés.
                </p>
              ) : (
                <SimpleBar style={{ height: "500px", opacity: "0.9" }}>
                  <ListGroup>{list}</ListGroup>
                </SimpleBar>
              )}
            </Col>
            <Col md={3}>
              <div
                style={{
                  width: "150px",
                  backgroundColor: "#000000",
                  textAlign: "center",
                  opacity: "0.7",
                  borderRadius: "5px",
                  marginBottom: "2%",
                  marginLeft: "15%"
                }}
              >
                <Image
                  style={{
                    border: "2px solid crimson",
                    objectFit: "cover"
                  }}
                  src={require("../img/adminPhoto.png")}
                  width="90px"
                  height="90px"
                  roundedCircle
                />
              </div>
              <div
                style={{
                  width: "150px",
                  backgroundColor: "#000000",
                  textAlign: "center",
                  opacity: "0.7",
                  borderRadius: "5px",
                  marginTop: "2%",
                  marginBottom: "2%",
                  marginLeft: "15%"
                }}
              >
                <p
                  style={{
                    color: "#02E7E0",
                    fontSize: "15px",
                    textAlign: "center",
                    marginBottom: "1%"
                  }}
                >
                  {name}
                </p>
              </div>
              <p
                style={{
                  color: "#99F7E7",
                  marginTop: "20%",
                  marginLeft: "10%",
                  width: "180px",
                  textAlign: "center",
                  height: "60px",
                  lineHeight: "60px",
                  backgroundColor: "#000000",
                  opacity: "0.7",
                  borderRadius: "5px"
                }}
              >
                Totalité :
              </p>
              <div
                style={{
                  width: "100px",
                  height: "30px",
                  backgroundColor: "#000000",
                  textAlign: "center",
                  opacity: "0.7",
                  borderRadius: "5px",
                  marginTop: "2%",
                  marginBottom: "2%",
                  marginLeft: "24%",
                  border: "1px solid #80D6E0"
                }}
              >
                <p style={{ color: "#8FFCAC" }}>
                  {this.state.notConfirmed.length}
                </p>
              </div>
              <OverlayTrigger
                key={"right-trash"}
                placement={"right"}
                overlay={
                  <Tooltip id={"tooltip-right"}>Supprimer la liste</Tooltip>
                }
              >
                <Button
                  disabled={this.state.notConfirmed.length === 0 ? true : false}
                  className="mr-3"
                  style={{
                    borderRadius: "50%",
                    marginTop: "10%",
                    marginLeft: "35%"
                  }}
                  variant="outline-primary"
                  onClick={e => {
                    e.preventDefault();
                    let arrayNotConfirmed = this.state.notConfirmed.map(
                      player => {
                        return player.email;
                      }
                    );
                    fetch(
                      "https://gamerapps.herokuapp.com/deletenotconfirmed",
                      {
                        method: "post",
                        headers: {
                          "Content-Type": "application/json"
                        },
                        body: JSON.stringify({
                          email: this.props.userData.email,
                          listPlayers: arrayNotConfirmed
                        })
                      }
                    )
                      .then(response => {
                        if (response) return response.json();
                      })
                      .then(data => {
                        this.setState({
                          notConfirmed: data
                        });
                      })
                      .catch(err => console.log(err));
                  }}
                >
                  <i className="fas fa-trash" />
                </Button>
              </OverlayTrigger>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    userData: state.userData
  };
};

export default connect(mapStateToProps)(NotConfirmedAccounts);
