import React from "react";
import {
  Modal,
  Button,
  Form,
  Row,
  Col,
  ButtonToolbar,
  Spinner
} from "react-bootstrap";

const ModalPassRemind = props => {
  const { onSubmit, onChange, onHide, sending, disabledbutton } = props;
  const { emailNotExist, emailIncorrect, googleAccount } = props.errors;
  return (
    <>
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header
          style={{ backgroundColor: "#00222A", color: "white" }}
          closeButton={false}
        >
          <Modal.Title
            style={{ margin: "0 auto" }}
            id="contained-modal-title-vcenter"
          >
            Saisissez votre adresse mail pour recevoir le nouveau mot de passe
          </Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ backgroundColor: "#00222A", color: "white" }}>
          <Form noValidate>
            <Row>
              <Col md={4} />
              <Col md={4}>
                <Form.Group controlId="formBasicEmailReminder">
                  <Form.Label>Votre email</Form.Label>
                  <Form.Control
                    onChange={onChange}
                    name="email"
                    type="email"
                    placeholder="email"
                  />
                  {emailNotExist === 1 ? (
                    <p style={{ color: "#F0DC4E" }}>Email n'existe pas</p>
                  ) : null}
                  {emailIncorrect === 1 ? (
                    <p style={{ color: "#F0DC4E" }}>
                      Email saisi n'est pas correct
                    </p>
                  ) : null}
                  {googleAccount === 1 ? (
                    <p style={{ color: "#F0DC4E" }}>
                      Compte Google. Impossible de changer mot de passe.
                    </p>
                  ) : null}
                </Form.Group>
              </Col>
              <Col md={4} />
            </Row>
          </Form>
        </Modal.Body>
        <Modal.Footer style={{ backgroundColor: "#00222A", color: "white" }}>
          <ButtonToolbar style={{ width: "40%" }}>
            <Button
              style={{ marginRight: "20%" }}
              onClick={onSubmit}
              variant="success"
              disabled={sending === 1 || disabledbutton === 1 ? true : false}
            >
              {sending === 1 && (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              )}
              {sending === 1 ? "J'envoie..." : "Envoyer"}
            </Button>
            <Button variant="info" onClick={onHide}>
              Annuler
            </Button>
          </ButtonToolbar>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default ModalPassRemind;
