import React, { Component } from "react";
import {
  Row,
  Col,
  Button,
  ButtonToolbar,
  Tooltip,
  OverlayTrigger,
  ListGroup,
  Container
} from "react-bootstrap";
import ModalGameForm from "./ModalGameForm";
import ModalGameModif from "./ModalGameModif";
import { connect } from "react-redux";
import { addUserData, addGameStat, clearGameStat } from "../redux/action";
import SimpleBar from "simplebar-react";
import "simplebar/dist/simplebar.min.css";

class ProfilGames extends Component {
  state = {
    isMounted: false,
    modalGameAddShow: false,
    modalGameModifShow: false,
    modifing: false,
    adding: false,
    deleting: false,
    gameDuplicated: false,
    gameToModif: "",
    modifMissionError: false,
    selectedGameTotalMissions: 0,
    name: "",
    games: [],
    arrayListGames: [],
    listFetchedGames: [],
    total_missions: 0,
    current_mission: 0,
    newMission: 0,
    formError: {
      error: false,
      errorMission: false
    }
  };
  componentDidMount() {
    let arrayListGames = this.props.userData.games.map(game => {
      return game.name;
    });
    fetch("https://gamerapps.herokuapp.com/givemerank", {
      method: "post",
      headers: this.props.userData.token
        ? {
            "x-auth": this.props.userData.token,
            "Content-Type": "application/json"
          }
        : {
            "Content-Type": "application/json"
          },
      body: JSON.stringify({
        isGoogleAccount: this.props.userData.isGoogleAccount,
        listGames: arrayListGames
      })
    })
      .then(response => {
        if (response) return response.json();
      })
      .then(data => {
        if (data !== null && data !== undefined) {
          this.setState({
            isMounted: true,
            games: this.props.userData.games,
            listFetchedGames: data
          });
        } else {
          this.setState({
            isMounted: true,
            games: this.props.userData.games
          });
        }
      })
      .catch(err => console.log(err));
  }

  handleChooseSubmits = action => {
    if (action === "trash") {
      this.handleDeleteGame();
    } else if (action === "modifMission") {
      this.handleGameModifSubmit();
    } else if (action === "stats") {
      this.handleStats();
    }
  };

  handleGameClick = game => {
    let selectedGame = this.props.userData.games.filter(
      elem => elem.name === game
    );
    this.state.isMounted &&
      this.setState({
        gameToModif: game,
        modalGameModifShow: true,
        selectedGameTotalMissions: selectedGame[0].total_missions
      });
  };

  handleStats = () => {
    this.state.isMounted &&
      this.props.addGameStatToRedux(this.state.gameToModif);
    this.state.isMounted &&
      this.setState({
        modalGameModifShow: false
      });
  };

  handleDeleteGame = () => {
    this.state.isMounted &&
      this.setState({
        deleting: true
      });
    fetch("https://gamerapps.herokuapp.com/deletegame", {
      method: "post",
      headers: this.props.userData.token
        ? {
            "x-auth": this.props.userData.token,
            "Content-Type": "application/json"
          }
        : {
            "Content-Type": "application/json"
          },
      body: JSON.stringify({
        email: this.props.userData.email,
        isGoogleAccount: this.props.userData.isGoogleAccount,
        game: this.state.gameToModif
      })
    })
      .then(response => {
        if (response) return response.json();
      })
      .then(data => {
        if (data) {
          this.state.isMounted && this.props.addUserDataToRedux(data);
          this.state.isMounted && this.props.clearGameStatFromRedux();
          this.state.isMounted &&
            this.setState({
              games: data.games,
              modalGameModifShow: false,
              deleting: false
            });
        }
      })
      .catch(err => console.log(err));
  };

  capitalize = text => {
    return text
      .toLowerCase()
      .split(" ")
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join(" ");
  };

  handleGameModifSubmit = () => {
    this.state.isMounted &&
      this.setState({
        modifing: true
      });
    let gameFiltered = [...this.state.games].filter(
      game => game.name === this.state.gameToModif
    );
    let lastIndex = gameFiltered[0].updates.length - 1;
    let lastMission = gameFiltered[0].updates[lastIndex].new_mission;
    if (parseInt(this.state.newMission) <= lastMission) {
      this.state.isMounted &&
        this.setState({
          modifing: false,
          modalGameModifShow: true,
          formError: {
            errorMission: true
          }
        });
    } else {
      fetch("https://gamerapps.herokuapp.com/changemission", {
        method: "post",
        headers: this.props.userData.token
          ? {
              "x-auth": this.props.userData.token,
              "Content-Type": "application/json"
            }
          : {
              "Content-Type": "application/json"
            },
        body: JSON.stringify({
          email: this.props.userData.email,
          isGoogleAccount: this.props.userData.isGoogleAccount,
          game: this.state.gameToModif,
          mission: this.state.newMission
        })
      })
        .then(response => {
          if (response) return response.json();
        })
        .then(data => {
          if (data.success) {
            this.state.isMounted && this.props.addUserDataToRedux(data.success);
            this.state.isMounted &&
              this.setState({
                games: data.success.games,
                modalGameModifShow: false,
                modifMissionError: false,
                modifing: false,
                newMission: 0,
                formError: {
                  errorMission: false
                }
              });
            let arrayListGames = this.props.userData.games.map(game => {
              return game.name;
            });
            fetch("https://gamerapps.herokuapp.com/givemerank", {
              method: "post",
              headers: this.props.userData.token
                ? {
                    "x-auth": this.props.userData.token,
                    "Content-Type": "application/json"
                  }
                : {
                    "Content-Type": "application/json"
                  },
              body: JSON.stringify({
                isGoogleAccount: this.props.userData.isGoogleAccount,
                listGames: arrayListGames
              })
            })
              .then(response => {
                if (response) return response.json();
              })
              .then(data => {
                if (data !== null && data !== undefined) {
                  this.state.isMounted &&
                    this.setState({
                      isMounted: true,
                      games: this.props.userData.games,
                      listFetchedGames: data
                    });
                } else {
                  this.state.isMounted &&
                    this.setState({
                      isMounted: true,
                      games: this.props.userData.games
                    });
                }
              })
              .catch(err => console.log(err));
          } else if (data.status === 1) {
            this.state.isMounted &&
              this.setState({
                modalGameModifShow: true,
                modifMissionError: true,
                modifing: false,
                formError: {
                  errorMission: false
                }
              });
          }
        })
        .catch(err => console.log(err));
    }
  };

  handleGameModifInputChange = e => {
    this.state.isMounted &&
      this.setState({
        ...this.state,
        [e.target.name]: e.target.value
      });
  };

  handleGameAddInputChange = e => {
    this.state.isMounted &&
      this.setState({
        ...this.state,
        [e.target.name]: e.target.value
      });
  };

  handleGameAddSubmit = e => {
    e.preventDefault();
    this.state.isMounted &&
      this.setState({
        adding: true
      });
    if (
      parseInt(this.state.current_mission) > parseInt(this.state.total_missions)
    ) {
      this.state.isMounted &&
        this.setState({
          adding: false,
          modalGameAddShow: true,
          gameDuplicated: false,
          formError: {
            error: true
          }
        });
    } else {
      const gameName = this.capitalize(this.state.name);
      fetch("https://gamerapps.herokuapp.com/insertgame", {
        method: "post",
        headers: this.props.userData.token
          ? {
              "x-auth": this.props.userData.token,
              "Content-Type": "application/json"
            }
          : {
              "Content-Type": "application/json"
            },
        body: JSON.stringify({
          email: this.props.userData.email,
          isGoogleAccount: this.props.userData.isGoogleAccount,
          game: {
            name: gameName,
            total_missions: this.state.total_missions,
            current_mission: this.state.current_mission
          }
        })
      })
        .then(response => {
          if (response) return response.json();
        })
        .then(data => {
          if (data.success) {
            this.state.isMounted && this.props.addUserDataToRedux(data.success);
            this.state.isMounted &&
              this.setState({
                games: data.success.games,
                adding: false,
                gameDuplicated: false,
                modalGameAddShow: false,
                name: "",
                total_missions: 0,
                current_mission: 0,
                formError: {
                  error: false
                }
              });
            let arrayListGames = this.props.userData.games.map(game => {
              return game.name;
            });
            fetch("https://gamerapps.herokuapp.com/givemerank", {
              method: "post",
              headers: this.props.userData.token
                ? {
                    "x-auth": this.props.userData.token,
                    "Content-Type": "application/json"
                  }
                : {
                    "Content-Type": "application/json"
                  },
              body: JSON.stringify({
                isGoogleAccount: this.props.userData.isGoogleAccount,
                listGames: arrayListGames
              })
            })
              .then(response => {
                if (response) return response.json();
              })
              .then(data => {
                if (data !== null && data !== undefined) {
                  this.state.isMounted &&
                    this.setState({
                      isMounted: true,
                      games: this.props.userData.games,
                      listFetchedGames: data
                    });
                } else {
                  this.state.isMounted &&
                    this.setState({
                      isMounted: true,
                      games: this.props.userData.games
                    });
                }
              })
              .catch(err => console.log(err));
          } else if (data.status === 1) {
            this.state.isMounted &&
              this.setState({
                adding: false,
                modalGameAddShow: true,
                gameDuplicated: true,
                formError: {
                  error: false
                }
              });
          }
        })
        .catch(err => console.log(err));
    }
  };

  componentWillUnmount() {
    this.setState({
      isMounted: false
    });
  }

  render() {
    let array = this.state.listFetchedGames.map(item =>
      item.games.map(game => {
        return { email: item.email, game: game };
      })
    );
    const { containerGames, buttonAdd, listItem, noGames } = this.props.styless;
    let games = [];
    let noGame = "";
    if (this.state.games.length === 0) {
      noGame = "Votre liste de jeux est vide.";
    } else {
      games = this.state.games.map((game, i) => {
        let progress = Math.round(
          (game.current_mission * 100) / game.total_missions
        );
        let filtered = array.map(item => {
          return item.filter(result => result.game.name === game.name);
        });
        let notEmpty = filtered.filter(item => item.length > 0);
        let filteredSorted = notEmpty.sort((a, b) => {
          if (a[0].game.current_mission === b[0].game.current_mission) {
            return new Date(
              a[0].game.updates[a[0].game.updates.length - 1].date
            ) < new Date(b[0].game.updates[b[0].game.updates.length - 1].date)
              ? -1
              : 1;
          } else {
            return a[0].game.current_mission > b[0].game.current_mission
              ? -1
              : 1;
          }
        });
        let rank = filteredSorted.findIndex(
          element => element[0].email === this.props.userData.email
        );
        return (
          <div key={i}>
            <ListGroup.Item
              style={listItem}
              action
              onClick={e => {
                e.preventDefault();
                this.handleGameClick(game.name);
              }}
            >
              <Row>
                <Col md={5}>
                  <p style={{ display: "inline-block", width: "200px" }}>
                    {game.name}
                  </p>{" "}
                </Col>
                <Col md={4}>
                  <div
                    style={{
                      display: "inline-block",
                      marginLeft: "10%",
                      width: "150px",
                      border: "1px solid white",
                      borderRadius: "3px",
                      backgroundColor: "#0A293D"
                    }}
                  >
                    <div
                      style={{
                        width: `${progress}%`,
                        backgroundColor: "crimson",
                        marginLeft: "0.2%"
                      }}
                    >
                      {`${progress}%`}
                    </div>
                  </div>
                </Col>
                <Col md={3}>
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "center"
                    }}
                  >
                    <div
                      style={{
                        width: "30px",
                        height: "30px",
                        borderRadius: "50%",
                        border: "2px solid #CC62B0",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        margin: "0 auto",
                        fontWeight: "bold",
                        color: "#FFFFD4",
                        backgroundColor: "#012028",
                        opcity: "0.9",
                        fontFamily: "Roboto-Mono",
                        fontSize: "10px"
                      }}
                    >
                      {rank + 1}
                    </div>
                    {rank + 1 === 1 && (
                      <div style={{ width: "5px", opacity: "0.9" }}>
                        <i
                          style={{ color: "yellow", opacity: "0.9" }}
                          className="fas fa-trophy"
                        />
                      </div>
                    )}
                    {rank + 1 > 1 && <div style={{ width: "5px" }}>&nbsp;</div>}
                  </div>
                </Col>
              </Row>
            </ListGroup.Item>
            <ModalGameModif
              show={this.state.modalGameModifShow}
              onSubmit={this.handleChooseSubmits}
              onChange={this.handleGameModifInputChange}
              onHide={() => {
                this.state.isMounted &&
                  this.setState({
                    modalGameModifShow: false,
                    modifMissionError: false,
                    newMission: 0,
                    formError: {
                      errorMission: false
                    }
                  });
              }}
              gametomodif={this.state.gameToModif}
              disabledbutton={this.state.newMission === 0 ? 1 : 0}
              modifing={this.state.modifing ? 1 : 0}
              deleting={this.state.deleting ? 1 : 0}
              error={this.state.modifMissionError ? 1 : 0}
              errormission={this.state.formError.errorMission ? 1 : 0}
              totalmissions={this.state.selectedGameTotalMissions}
            />
          </div>
        );
      });
    }
    return (
      <div style={containerGames}>
        <Container>
          <Row>
            <Col md={3} />
            <Col md={2}>
              <ButtonToolbar>
                <OverlayTrigger
                  key={"right"}
                  placement={"right"}
                  overlay={
                    <Tooltip id={"tooltip-right"}>Ajouter un jeu</Tooltip>
                  }
                >
                  <Button
                    style={buttonAdd}
                    variant="outline-primary"
                    onClick={e => {
                      e.preventDefault();
                      this.state.isMounted &&
                        this.setState({
                          modalGameAddShow: true
                        });
                    }}
                  >
                    <i className="fas fa-plus" />
                  </Button>
                </OverlayTrigger>
              </ButtonToolbar>
              <ModalGameForm
                show={this.state.modalGameAddShow}
                disabledbutton={
                  this.state.name === "" ||
                  this.state.total_missions === 0 ||
                  this.state.current_mission === 0
                    ? 1
                    : 0
                }
                adding={this.state.adding ? 1 : 0}
                gameduplicated={this.state.gameDuplicated ? 1 : 0}
                error={this.state.formError.error ? 1 : 0}
                onSubmit={this.handleGameAddSubmit}
                onChange={this.handleGameAddInputChange}
                onHide={() => {
                  this.state.isMounted &&
                    this.setState({
                      modalGameAddShow: false,
                      adding: false,
                      gameDuplicated: false,
                      name: "",
                      total_missions: 0,
                      current_mission: 0,
                      formError: {
                        error: false
                      }
                    });
                }}
              />
            </Col>
            <Col md={7} />
          </Row>
          <Row>
            <Col md={2} />
            <Col md={7}>
              {noGame ? (
                <p style={noGames}>{noGame}</p>
              ) : (
                <SimpleBar
                  style={{ height: "300px", marginTop: "3%", opacity: "0.9" }}
                >
                  <ListGroup>{games}</ListGroup>
                </SimpleBar>
              )}
            </Col>
            <Col md={3} />
          </Row>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    userData: state.userData,
    gameStat: state.gameStat
  };
};

const mapDispatchToProps = dispatch => {
  return {
    addUserDataToRedux: data => dispatch(addUserData(data)),
    addGameStatToRedux: data => dispatch(addGameStat(data)),
    clearGameStatFromRedux: () => dispatch(clearGameStat())
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ProfilGames);
