import React from "react";
import { Form, Row, Col, Button, Spinner } from "react-bootstrap";

const SearchGamerForm = props => {
  const { change, submit, searching, disabledbutton } = props;
  return (
    <div
      style={{ position: "absolute", top: "15%", left: "10%", right: "30%" }}
    >
      <Form onSubmit={submit}>
        <Row
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center"
          }}
        >
          <Col md={4} />
          <Col md={4}>
            <Form.Group controlId="formBasicGame">
              <Form.Label style={{ color: "white" }}>
                Qui encore joue mon jeu ?
              </Form.Label>
              <Form.Control
                onChange={change}
                name="game"
                type="text"
                placeholder="Saisissez le nom du jeu"
              />
            </Form.Group>
          </Col>
          <Col md={4}>
            <Button
              style={{ marginTop: "4%" }}
              variant="primary"
              type="submit"
              disabled={searching === 1 || disabledbutton === 1 ? true : false}
            >
              {searching === 1 && (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              )}
              {searching === 1 ? "Je cherche..." : "Rechercher"}
            </Button>
          </Col>
        </Row>
      </Form>
    </div>
  );
};

export default SearchGamerForm;
