import React from "react";
import {
  Modal,
  Button,
  Form,
  Row,
  Col,
  ButtonToolbar,
  Spinner
} from "react-bootstrap";

const ModalGameForm = props => {
  const {
    onSubmit,
    onChange,
    onHide,
    adding,
    error,
    gameduplicated,
    disabledbutton
  } = props;
  return (
    <>
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header
          style={{ backgroundColor: "#00222A", color: "white" }}
          closeButton={false}
        >
          <Modal.Title
            style={{ margin: "0 auto" }}
            id="contained-modal-title-vcenter"
          >
            Ajouter un nouveau jeu
          </Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ backgroundColor: "#00222A", color: "white" }}>
          <Form noValidate>
            <Row>
              <Col md={4} />
              <Col md={4}>
                <Form.Group controlId="formBasicGameName">
                  <Form.Label>Nom du jeu copié de l'emballage</Form.Label>
                  <Form.Control
                    onChange={onChange}
                    name="name"
                    type="text"
                    placeholder="nom du jeu"
                  />
                  {gameduplicated === 1 ? (
                    <p style={{ color: "#F0DC4E" }}>Jeu déjà existe.</p>
                  ) : null}
                </Form.Group>
              </Col>
              <Col md={4} />
            </Row>
            <Row>
              <Col md={4} />
              <Col md={4}>
                <Form.Group controlId="formBasicGameTotalMissions">
                  <Form.Label>Nombre des missions</Form.Label>
                  <Form.Control
                    onChange={onChange}
                    name="total_missions"
                    type="number"
                    placeholder="nombre des missions"
                  />
                </Form.Group>
              </Col>
              <Col md={4} />
            </Row>
            <Row>
              <Col md={4} />
              <Col md={4}>
                <Form.Group controlId="formBasicGameCurrentMission">
                  <Form.Label>Mission actuelle</Form.Label>
                  <Form.Control
                    onChange={onChange}
                    name="current_mission"
                    type="number"
                    placeholder="mission actuelle"
                  />
                  {error === 1 ? (
                    <p style={{ color: "#F0DC4E" }}>
                      Mission actuelle supérieure à la totalité des missions.
                    </p>
                  ) : null}
                </Form.Group>
              </Col>
              <Col md={4} />
            </Row>
          </Form>
        </Modal.Body>
        <Modal.Footer style={{ backgroundColor: "#00222A", color: "white" }}>
          <ButtonToolbar style={{ width: "40%" }}>
            <Button
              style={{ marginRight: "20%" }}
              onClick={onSubmit}
              variant="success"
              disabled={adding === 1 || disabledbutton === 1 ? true : false}
            >
              {adding === 1 && (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              )}
              {adding === 1 ? "J'ajoute..." : "Ajouter"}
            </Button>
            <Button variant="info" onClick={onHide}>
              Annuler
            </Button>
          </ButtonToolbar>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default ModalGameForm;
