const initialState = {
  gameStat: "Statistiques"
};

const gameReducer = (state = initialState, action) => {
  switch (action.type) {
    case "addGameStat":
      return (state = action.payload);
    case "clearGameStat":
      return (state = initialState.gameStat);
    default:
      return state;
  }
};

export default gameReducer;
