import React from "react";
import { NavLink } from "react-router-dom";
import { Nav, Image, Row, Col } from "react-bootstrap";
import "../styles/Navbar.css";
import { clearUserData, clearGameStat } from "../redux/action";
import { useSelector, useDispatch } from "react-redux";

const navItems = [
  { name: "Connexion", path: "/" },
  { name: "S'inscrire", path: "/signup" },
  { name: "à propos", path: "/about" }
];

const navItemsUserLogged = [
  { name: "Trouver un joueur", path: "/searchgamer" },
  { name: "Profil", path: "/profil" },
  { name: "Déconnexion", path: "/logout" }
];

const navItemsAdmin = [
  { name: "Afficher tous", path: "/allaccounts" },
  { name: "Afficher non confirmés", path: "/notcomfirmedaccounts" },
  { name: "Déconnexion", path: "/logout" }
];

const styles = {
  navlink: {
    marginRight: "40px",
    textDecoration: "none"
  }
};

const Navbar = () => {
  const { token, isGoogleAccount, isAdmin } = useSelector(
    state => state.userData
  );
  const dispatch = useDispatch();
  const menu = navItems.map(item => (
    <Nav.Item as="li" key={item.name}>
      <NavLink
        style={styles.navlink}
        to={item.path}
        exact
        activeClassName="active"
      >
        {item.name}
      </NavLink>
    </Nav.Item>
  ));

  const menuAdmin = navItemsAdmin.map(item => (
    <Nav.Item as="li" key={item.name}>
      <NavLink
        onClick={
          item.name === "Déconnexion"
            ? e => {
                e.preventDefault();
                dispatch(clearUserData());
                dispatch(clearGameStat());
              }
            : null
        }
        style={styles.navlink}
        to={item.path}
        exact
        activeClassName="active"
      >
        {item.name}
      </NavLink>
    </Nav.Item>
  ));

  const menuUserLogged = navItemsUserLogged.map(item => (
    <Nav.Item as="li" key={item.name}>
      <NavLink
        onClick={
          item.name === "Déconnexion"
            ? e => {
                e.preventDefault();
                dispatch(clearUserData());
                dispatch(clearGameStat());
              }
            : null
        }
        style={styles.navlink}
        to={item.path}
        exact
        activeClassName="active"
      >
        {item.name}
      </NavLink>
    </Nav.Item>
  ));
  return (
    <Row>
      <Col md={1}>
        <Image
          style={{ backgroundColor: "black", opacity: "0.8" }}
          width="110px"
          src={require("../img/logo.JPG")}
          roundedCircle
        />
      </Col>
      <Col md={10}>
        <Nav fill className="justify-content-end" as="ul">
          {token || isGoogleAccount
            ? menuUserLogged
            : isAdmin
            ? menuAdmin
            : menu}
        </Nav>
      </Col>
      <Col md={1} />
    </Row>
  );
};

export default Navbar;
