import React, { Component } from "react";
import { connect } from "react-redux";
import { Row, Col, ListGroup, Container } from "react-bootstrap";
import SimpleBar from "simplebar-react";
import ProfilGameProgressTitle from "./ProfilGameProgressTitle";

class ProfilGameProgress extends Component {
  state = {
    isMounted: false,
    gameStat: "",
    games: []
  };

  componentDidMount() {
    this.setState({
      isMounted: true,
      games: this.props.userData.games,
      gameStat: this.props.gameStat.gameStat
    });
  }

  componentWillReceiveProps(nextProps) {
    if (
      this.props.gameStat !== nextProps.gameStat ||
      this.props.userData.games !== nextProps.userData.games
    ) {
      this.setState({
        gameStat: nextProps.gameStat,
        games: nextProps.userData.games
      });
    }
  }

  componentWillUnmount() {
    this.setState({
      isMounted: false
    });
  }

  render() {
    const { containerProgress } = this.props.stylesss;
    let itemInitial;
    let gameFiltered = [];
    if (this.state.gameStat) {
      gameFiltered = [...this.state.games].filter(
        game => game.name === this.state.gameStat
      );
      if (gameFiltered.length > 0) {
        let updatesArray = gameFiltered[0].updates;
        let resSubtratMissions = updatesArray.slice(1).map((item, i) => {
          return {
            new_mission: item.new_mission - updatesArray[i].new_mission,
            date: item.date
          };
        });

        updatesArray.length > 1
          ? (itemInitial = resSubtratMissions.map((update, i) => {
              let dayProgress = Math.round((update.new_mission * 100) / 15);
              let missions = update.new_mission;
              let date = new Date(update.date);
              return (
                <div style={{ width: "100px" }} key={i}>
                  {i === 0 && (
                    <span
                      style={{
                        display: "block",
                        width: "115px",
                        fontSize: "10px",
                        color: "white",
                        textAlign: "start",
                        margin: "2%"
                      }}
                    >
                      But : 15 missions par date
                    </span>
                  )}
                  <ListGroup.Item
                    style={{
                      backgroundColor: "#00222A",
                      width: "200px",
                      marginTop: "10%",
                      padding: "0",
                      display: "inline-block",
                      borderRadius: "3px",
                      border: "1.5px solid crimson"
                    }}
                  >
                    <div
                      style={{
                        backgroundColor: "#E88574",
                        borderRadius: "2px",
                        width: `${dayProgress}%`
                      }}
                    >
                      &nbsp;
                    </div>
                  </ListGroup.Item>
                  <div
                    style={{ display: "flex", justifyContent: "space-between" }}
                  >
                    <span
                      style={{
                        width: "65px",
                        fontSize: "10px",
                        color: "white",
                        verticalAlign: "middle"
                      }}
                    >
                      {date.getDate() > 1 && date.getDate() < 10
                        ? `0${date.getDate()}`
                        : date.getDate()}
                      /
                      {date.getMonth() + 1 > 1 && date.getMonth() + 1 < 10
                        ? `0${date.getMonth() + 1}`
                        : date.getMonth() + 1}
                      /{date.getFullYear()} à{" "}
                      {date.getHours() >= 0 && date.getHours() < 10
                        ? `0${date.getHours()}`
                        : date.getHours()}
                      h
                      {date.getMinutes() > 1 && date.getMinutes() < 10
                        ? `0${date.getMinutes()}`
                        : date.getMinutes()}
                    </span>
                    <span
                      style={{
                        marginLeft: "15%",
                        fontSize: "10px",
                        width: "155px",
                        color: "#F5A8FF",
                        textAlign: "center"
                      }}
                    >
                      {missions} {missions === 1 ? "mission" : "missions"}
                    </span>
                  </div>
                </div>
              );
            }))
          : (itemInitial = (
              <div
                style={{
                  width: "350px",
                  height: "50px",
                  marginLeft: "20%",
                  borderRadius: "5px",
                  textAlign: "center",
                  lineHeight: "50px",
                  backgroundColor: "#1B1D1E",
                  opacity: "0.9",
                  color: "white"
                }}
              >
                Les statistiques indisponible en ce moment.
              </div>
            ));
      }
    }
    return (
      <div style={containerProgress}>
        <Container>
          <Row>
            <Col md={2} />
            <Col md={8}>
              <ProfilGameProgressTitle gamestat={this.state.gameStat} />
            </Col>
            <Col md={2} />
          </Row>
          <Row>
            <Col md={3} />
            <Col md={6}>
              <SimpleBar
                style={{
                  height: "300px",
                  opacity: "0.9"
                }}
              >
                <ListGroup>{itemInitial}</ListGroup>
              </SimpleBar>
            </Col>
            <Col md={3} />
          </Row>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    userData: state.userData,
    gameStat: state.gameStat
  };
};

export default connect(mapStateToProps)(ProfilGameProgress);
