const initialState = {
  delAccount: ""
};

const delAccountReducer = (state = initialState, action) => {
  switch (action.type) {
    case "addDelAccount":
      return (state = action.payload);
    case "clearDelAccount":
      return (state = initialState.delAccount);
    default:
      return state;
  }
};

export default delAccountReducer;
