import React, { Component } from "react";
import SearchGamerForm from "../component/SearchGamerForm";
import ListGamer from "../component/ListGamer";
import { connect } from "react-redux";

class SearchGamer extends Component {
  state = {
    isMounted: false,
    searching: false,
    game: "",
    gameTitle: "",
    listFetchedUsers: []
  };

  componentDidMount() {
    this.setState({
      isMounted: true
    });
  }

  capitalize = text => {
    return text
      .toLowerCase()
      .split(" ")
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join(" ");
  };

  handleGamerOnChange = e => {
    this.state.isMounted &&
      this.setState({
        ...this.state,
        [e.target.name]: this.capitalize(e.target.value)
      });
  };

  handleGamerSubmit = e => {
    e.preventDefault();
    this.state.isMounted &&
      this.setState({
        searching: true
      });
    fetch("https://gamerapps.herokuapp.com/givemerank", {
      method: "post",
      headers: this.props.userData.token
        ? {
            "x-auth": this.props.userData.token,
            "Content-Type": "application/json"
          }
        : {
            "Content-Type": "application/json"
          },
      body: JSON.stringify({
        isGoogleAccount: this.props.userData.isGoogleAccount,
        listGames: [this.state.game]
      })
    })
      .then(response => {
        if (response) return response.json();
      })
      .then(data => {
        if (data !== null && data !== undefined) {
          this.state.isMounted &&
            this.setState({
              isMounted: true,
              searching: false,
              gameTitle: this.state.game,
              listFetchedUsers: data
            });
        }
      })
      .catch(err => console.log(err));
  };

  componentWillUnmount() {
    this.setState({
      isMounted: false
    });
  }

  render() {
    return (
      <>
        <SearchGamerForm
          change={this.handleGamerOnChange}
          submit={this.handleGamerSubmit}
          searching={this.state.searching ? 1 : 0}
          disabledbutton={this.state.game === "" ? 1 : 0}
        />
        <ListGamer
          players={this.state.listFetchedUsers}
          gametitle={this.state.gameTitle}
          getdata={this.getData}
        />
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    userData: state.userData
  };
};

export default connect(mapStateToProps)(SearchGamer);
