import React from "react";

const ProfilGameProgressTitle = props => {
  let gameStat = props.gamestat;
  let array = [];
  array.push(gameStat);
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        margin: "3% auto",
        color: "#FFA580",
        width: "250px",
        height: "40px",
        backgroundColor: "#001F26",
        borderRadius: "5px",
        textAlign: "center",
        fontSize: "14px",
        border: "1px solid #03A0A6",
        opacity: "0.9"
      }}
    >
      {array.map((string, i) => {
        console.log(string);
        return <span key={i}>{string}</span>;
      })}
    </div>
  );
};

export default ProfilGameProgressTitle;
