import React, { Component } from "react";
import { Container, Row, Col, ListGroup } from "react-bootstrap";
import SimpleBar from "simplebar-react";
import { connect } from "react-redux";

class ListGamer extends Component {
  state = {
    isMounted: false,
    userGames: []
  };

  componentDidMount() {
    let userGameNames = this.props.userData.games.map(game => {
      return game.name;
    });
    this.setState({
      isMounted: true,
      userGames: userGameNames
    });
  }

  componentWillUnmount() {
    this.setState({
      isMounted: false
    });
  }
  render() {
    let gameTitle = this.props.gametitle;
    let arrayPlayers = this.props.players.map(item =>
      item.games.map(game => {
        return { email: item.email, name: item.name, game: game };
      })
    );
    let filteredByGame = [];
    if (this.state.userGames.includes(gameTitle)) {
      filteredByGame = arrayPlayers.map(item => {
        return item.filter(result => result.game.name === gameTitle);
      });
    }
    let listSorted = filteredByGame.sort((a, b) => {
      if (a[0].game.current_mission === b[0].game.current_mission) {
        return new Date(a[0].game.updates[a[0].game.updates.length - 1].date) <
          new Date(b[0].game.updates[b[0].game.updates.length - 1].date)
          ? -1
          : 1;
      } else {
        return a[0].game.current_mission > b[0].game.current_mission ? -1 : 1;
      }
    });
    let item = listSorted.map((player, i) => {
      let progress = Math.round(
        (player[0].game.current_mission * 100) / player[0].game.total_missions
      );
      let rank = listSorted.findIndex(
        element => element[0].email === player[0].email
      );
      return (
        <div key={i}>
          {player[0].email !== this.props.userData.email && (
            <ListGroup.Item
              style={{
                backgroundColor: "#00222A",
                textAlign: "justify",
                color: "#01EBE2",
                fontSize: "14px",
                height: "50px"
              }}
              action
            >
              <Row>
                <Col md={5}>
                  <p style={{ display: "inline-block", width: "200px" }}>
                    {player[0].name}
                  </p>{" "}
                </Col>
                <Col md={4}>
                  <div
                    style={{
                      display: "inline-block",
                      marginLeft: "10%",
                      width: "150px",
                      border: "1px solid white",
                      borderRadius: "3px",
                      backgroundColor: "#0A293D"
                    }}
                  >
                    <div
                      style={{
                        width: `${progress}%`,
                        backgroundColor: "crimson",
                        marginLeft: "0.2%"
                      }}
                    >
                      {`${progress}%`}
                    </div>
                  </div>
                </Col>
                <Col md={3}>
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "center"
                    }}
                  >
                    <div
                      style={{
                        width: "30px",
                        height: "30px",
                        borderRadius: "50%",
                        border: "2px solid #CC62B0",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        margin: "0 auto",
                        fontWeight: "bold",
                        color: "#FFFFD4",
                        backgroundColor: "#012028",
                        opcity: "0.9",
                        fontFamily: "Roboto-Mono",
                        fontSize: "10px"
                      }}
                    >
                      {rank + 1}
                    </div>
                    {rank + 1 === 1 && (
                      <div style={{ width: "5px", opacity: "0.9" }}>
                        <i
                          style={{ color: "yellow", opacity: "0.9" }}
                          className="fas fa-trophy"
                        />
                      </div>
                    )}
                    {rank + 1 > 1 && <div style={{ width: "5px" }}>&nbsp;</div>}
                  </div>
                </Col>
              </Row>
            </ListGroup.Item>
          )}
        </div>
      );
    });
    return (
      <div
        style={{
          position: "absolute",
          top: "28%",
          left: "10%",
          right: "10%"
        }}
      >
        <Container>
          {this.props.players.length <= 1 && (
            <Row>
              <Col md={3} />
              <Col md={3}>
                <p
                  style={{
                    position: "absolute",
                    left: "5%",
                    width: "220px",
                    height: "50px",
                    borderRadius: "5px",
                    textAlign: "center",
                    lineHeight: "50px",
                    backgroundColor: "#00222A",
                    color: "#C3E895",
                    opacity: "0.9"
                  }}
                >
                  Pas de résultats.
                </p>
              </Col>
              <Col md={6} />
            </Row>
          )}
          {this.props.players.length > 1 && (
            <Row>
              <Col md={4} />
              <Col md={4}>
                <p
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    margin: "3% auto",
                    color: "#BEFFBA",
                    width: "250px",
                    height: "40px",
                    backgroundColor: "#2D4030",
                    borderRadius: "5px",
                    textAlign: "center",
                    fontSize: "14px",
                    border: "1px solid #03A0A6",
                    opacity: "0.9"
                  }}
                >
                  {gameTitle}
                </p>
              </Col>
              <Col md={4} />
            </Row>
          )}
          <Row>
            <Col md={2} />
            <Col md={7}>
              <SimpleBar style={{ height: "300px", opacity: "0.9" }}>
                <ListGroup>{item}</ListGroup>
              </SimpleBar>
            </Col>
            <Col md={3} />
          </Row>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    userData: state.userData
  };
};

export default connect(mapStateToProps)(ListGamer);
