import React from "react";
import { Container, Row, Col } from "react-bootstrap";

const About = () => {
  return (
    <div
      style={{ position: "absolute", top: "10%", left: "10%", right: "10%" }}
    >
      <Container>
        <Row>
          <Col md={1} />
          <Col md={10}>
            <section>
              <div
                style={{
                  backgroundColor: "black",
                  opacity: "0.8",
                  borderRadius: "5px",
                  height: "600px",
                  marginTop: "2%"
                }}
              >
                <p
                  style={{
                    color: "#FFC4BD",
                    fontSize: "55px",
                    textAlign: "left",
                    padding: "30px",
                    fontFamily: "Roboto",
                    lineHeight: "115px",
                    verticalAlign: "center"
                  }}
                >
                  Créez votre propre portefeuille des jeux de vidéo. Suivez
                  votre progrès, comparez vos résultats avec d'autres joueurs.
                  Faites tout cela et même plus grâce à Gamer App.
                </p>
              </div>
            </section>
          </Col>
          <Col md={1} />
        </Row>
        <Row>
          <Col md={1} />
          <Col md={10}>
            <footer
              style={{
                display: "flex",
                justifyContent: "space-between",
                backgroundColor: "black",
                opacity: "0.8",
                borderRadius: "5px",
                height: "65px",
                lineHeight: "65px",
                marginTop: "10%"
              }}
            >
              <p
                style={{
                  display: "inline-block",
                  fontSize: "20px",
                  color: "#8992B3",
                  paddingLeft: "15px",
                  fontFamily: "DaFont"
                }}
              >
                &#169; {new Date().getFullYear()} Paul Del Priore
              </p>
              <p
                style={{
                  display: "inline-block",
                  fontSize: "20px",
                  color: "#8992B3",
                  paddingRight: "15px",
                  fontFamily: "DaFont"
                }}
              >
                contact : paul.del.priore29@gmail.com
              </p>
            </footer>
          </Col>
          <Col md={1} />
        </Row>
      </Container>
    </div>
  );
};

export default About;
