const initialState = {
  name: "",
  age: "",
  email: "",
  city: "",
  country: "",
  games: []
};

const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case "addUserData":
      return (state = action.payload);
    case "clearUserData":
      return (state = initialState);
    default:
      return state;
  }
};

export default userReducer;
