import React, { Component } from "react";
import { Row, Col, Container, ListGroup, Image } from "react-bootstrap";
import SimpleBar from "simplebar-react";
import "simplebar/dist/simplebar.min.css";
import { connect } from "react-redux";

class AllAccounts extends Component {
  state = {
    loading: false,
    all: []
  };

  componentDidMount() {
    this.setState({
      loading: true
    });
    fetch("https://gamerapps.herokuapp.com/allaccounts", {
      method: "post",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        email: this.props.userData.email
      })
    })
      .then(response => {
        if (response) return response.json();
      })
      .then(data => {
        if (data) {
          this.setState({
            loading: false,
            all: data
          });
        }
      })
      .catch(err => console.log(err));
  }

  render() {
    const name = this.props.userData.name;
    let list = this.state.all.map((item, i) => {
      return this.state.loading ? (
        <p
          style={{
            position: "absolute",
            top: "15%",
            left: "5%",
            width: "220px",
            height: "50px",
            borderRadius: "5px",
            textAlign: "center",
            lineHeight: "50px",
            backgroundColor: "#00222A",
            color: "#C3E895",
            opacity: "0.9"
          }}
        >
          En cours de chargement ...
        </p>
      ) : (
        <div key={i}>
          {item.name !== name && (
            <ListGroup.Item
              style={{
                backgroundColor: "#00222A",
                textAlign: "justify",
                color: "#FFB49E",
                fontSize: "14px",
                height: "50px"
              }}
            >
              <Row>
                <Col md={4}>
                  <p>{item.name}</p>
                </Col>
                <Col md={6}>
                  <p>{item.email}</p>
                </Col>
                <Col md={2}>
                  <p>
                    {new Date(item.creation_date).getDate() > 1 &&
                    new Date(item.creation_date).getDate() < 10
                      ? `0${new Date(item.creation_date).getDate()}`
                      : new Date(item.creation_date).getDate()}
                    /
                    {new Date(item.creation_date).getMonth() + 1 > 1 &&
                    new Date(item.creation_date).getMonth() + 1 < 10
                      ? `0${new Date(item.creation_date).getMonth() + 1}`
                      : new Date(item.creation_date).getMonth() + 1}
                    /{new Date(item.creation_date).getFullYear()}
                  </p>
                </Col>
              </Row>
            </ListGroup.Item>
          )}
        </div>
      );
    });
    return (
      <div
        style={{
          position: "absolute",
          top: "15%",
          left: "5%",
          right: "5%",
          bottom: "5%"
        }}
      >
        <Container>
          <Row>
            <Col md={2} />
            <Col md={7}>
              <SimpleBar style={{ height: "500px", opacity: "0.9" }}>
                <ListGroup>{list}</ListGroup>
              </SimpleBar>
            </Col>
            <Col md={3}>
              <div
                style={{
                  width: "150px",
                  backgroundColor: "#000000",
                  textAlign: "center",
                  opacity: "0.7",
                  borderRadius: "5px",
                  marginBottom: "2%",
                  marginLeft: "15%"
                }}
              >
                <Image
                  style={{
                    border: "2px solid crimson",
                    objectFit: "cover"
                  }}
                  src={require("../img/adminPhoto.png")}
                  width="90px"
                  height="90px"
                  roundedCircle
                />
              </div>
              <div
                style={{
                  width: "150px",
                  backgroundColor: "#000000",
                  textAlign: "center",
                  opacity: "0.7",
                  borderRadius: "5px",
                  marginTop: "2%",
                  marginBottom: "2%",
                  marginLeft: "15%"
                }}
              >
                <p
                  style={{
                    color: "#02E7E0",
                    fontSize: "15px",
                    textAlign: "center",
                    marginBottom: "1%"
                  }}
                >
                  {name}
                </p>
              </div>
              <p
                style={{
                  color: "#99F7E7",
                  marginTop: "20%",
                  marginLeft: "10%",
                  width: "180px",
                  textAlign: "center",
                  height: "60px",
                  lineHeight: "60px",
                  backgroundColor: "#000000",
                  opacity: "0.7",
                  borderRadius: "5px"
                }}
              >
                Totalité des comptes :
              </p>
              <div
                style={{
                  width: "100px",
                  height: "30px",
                  backgroundColor: "#000000",
                  textAlign: "center",
                  opacity: "0.7",
                  borderRadius: "5px",
                  marginTop: "2%",
                  marginBottom: "2%",
                  marginLeft: "24%",
                  border: "1px solid #80D6E0"
                }}
              >
                <p style={{ color: "#8FFCAC" }}>{this.state.all.length - 1}</p>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    userData: state.userData
  };
};

export default connect(mapStateToProps)(AllAccounts);
